import React, { Fragment } from "react";
import axios from "axios";
import Footer from "../component/Footer";
import HeaderDetail from "../component/header/HeaderDetail";
import SectionShare from "../component/detailPage/SectionShare";
import SectionContent from "../component/detailPage/SectionContent";
import Loading from "../component/Loading";

// css
import "../index.css";
import "@splidejs/react-splide/css";

class DetailPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      share: false,
      headClass: "h-screen overflow-y-scroll",
      search: false,
      searchValue: "",
      data: [],
      selectedColor: 0,
      loading: true,
    };
  }

  async componentDidMount() {
    this.setState({ loading: true });
    window.scrollTo(0, 0);

    axios.defaults.headers.common["X-Requested-With"] = "XMLHttpRequest";
    const res = await axios
      .get("http://localhost:8080/test/detail", {
        params: {
          token: "khaylila",
        },
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        this.setState({ data: res.data, loading: false });
        console.log(res.data);
      });
  }

  shareHandler = (value) => {
    this.setState({
      share: value,
      headClass: value === true ? "overflow-y-hidden max-h-screen" : "h-screen overflow-y-scroll",
    });
  };

  searchOnChangeHandler = (value) => {
    this.setState({
      search: true,
      searchValue: value,
    });
  };

  emptySearchOnClickHandler = () => {
    this.setState({
      searchValue: "",
    });
  };

  disableSearchHandler = () => {
    this.setState({
      search: false,
    });
  };

  changeColorHandler = (value) => {
    this.setState({ selectedColor: value });
  };

  render() {
    if (this.state.loading === true) {
      return (
        <Fragment>
          <Loading />
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <div id="mainContent" className={this.state.headClass}>
            <HeaderDetail
              shareOnClick={(value) => {
                this.shareHandler(value);
              }}
              search={this.state.search}
              searchValue={this.state.searchValue}
              searchOnChange={(value) => {
                this.searchOnChangeHandler(value);
              }}
              disableSearch={(value) => {
                this.disableSearchHandler(value);
              }}
              emptySearch={this.emptySearchOnClickHandler}
            />
            <SectionContent
              search={this.state.search}
              changeColor={(value) => {
                this.changeColorHandler(value);
              }}
              data={this.state.data}
              selectedColor={this.state.selectedColor}
              // listColor={this.state.data.listColors}
            />
            <SectionShare
              enable={this.state.share}
              shareOnClick={(value) => {
                this.shareHandler(value);
              }}
            />
            {this.state.search !== true ? <Footer /> : ""}
          </div>
        </Fragment>
      );
    }
  }
}

export default DetailPage;
